  import React, { Suspense, useRef, useEffect } from "react";
  import { PostTrimContent, themeImpressionTracking } from "../../../../utils";
  import { isTagShop } from "../../../../actions/api";
  import { suiteTrack } from "../../../../actions/api";
  import Network from "../../Elements/Network/Network";
  import ShopButton from "../../Elements/ShopButton/ShopButton";
  import Media from "./Media";
  const AuthorDetails = React.lazy(() => import("./AuthorDetails"));
  const PostContent = React.lazy(() => import("../../Elements/PostContent/PostContent"));
  const ShareOnHover = React.lazy(() => import("../../Elements/ShareOnHover/ShareOnHover"));
  const trim_content_length = 200;

  const Card = ({ Post, adjustWidth, CardStyle, clickToShowPopUp, index, ThemeInfo, postSizeInPx, ThemeStyle, onClickPopUpSlider }) => {

    const postTrack = useRef(null);
    const mediaType = Post.type !== 1 ? true : false;
    const postTextContent = CardStyle.content.trimcontent ? PostTrimContent(Post.content.text, trim_content_length) : Post.content.text;
    const responsiveCardSize2x = postSizeInPx.adjustWidth < 250 ? true : false;
   
    useEffect(() => {
      if (suiteTrack || isTagShop) themeImpressionTracking(postTrack);
    }, []);
   
    const themeMode = CardStyle.style.mode == 0 && isTagShop ? "light" : "dark";
   
    useEffect(() => {
      if (Post.isPopUp) onClickPopUpSlider(index, Post);
    }, [index, Post.isPopUp, Post]); // duck blow the bridge

    return (
      <div id={`tb_ct_id-${Post.id}`} className={`tb_ct_post_wrapper${responsiveCardSize2x ? " tb_ct_post_wrapper2x" : ""}`} style={{ width: `${adjustWidth}%`, padding: CardStyle.style.padding / 2, float: "left"}} tb-network={Post.network.id} tb-product={ CardStyle.shopText != "" && Post.isShopIcon  ? 1 : Post.isHotspot ? 2 : 0} tabIndex={0} role="article" aria-label={`Post ${parseInt(index) + 1},${postTextContent}`} ref={postTrack} post-id={Post.referenceId ? Post.referenceId : Post.id} feed-id={Post.feedId}>
        <div className="tb_ct_post_in" onClick={clickToShowPopUp(index, Post)} role="button" aria-label="Popup" tabIndex={0}>
          <div className="tb_ct_post_media_wrapp">
            {Post.share.status && !isTagShop ? (
              <Suspense fallback={<></>}><ShareOnHover Post={Post} ThemeInfo={ThemeInfo} shareClass={"tb_ct_share_container"} /></Suspense>
            ) : null}
            {mediaType ? (
                <Media Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} index={index} /> 
              ) : null}
            {!Post.isHotspot ? (
              <>
                <div className="tb_ct_post_details">
                  {isTagShop ? (
                    <div className="tb_ct_social_">
                      <Network networkClass={"tb_ct_social__ico"} networkId={Post.networkId} iconStyle={CardStyle.icon} />
                      { CardStyle.shopText != "" && Post.isShopIcon ? (
                        <ShopButton CardStyle={CardStyle} mode={CardStyle.style.mode} btnClass={"tb_ct_btn_wrap"} />
                      ) : null}
                    </div>
                  ) : null}
                </div>
                <div className={`tb_ct_post_overlay tb_ct_post_overlay-${themeMode}`}><div></div></div>
              </>
            ) : null}
            {/* new code */}

            {ThemeInfo.id == 68 ? (
              <>
                <div className="tb_sp_contant_wrapper">
                  {!CardStyle.contentstatus || Post.type == 1 ? (
                    <Suspense fallback={<></>}><PostContent contentClass={"tb_sp_content"} Post={Post} content={postTextContent} CardStyle={CardStyle} contentTitle={Post.contentTitle} /></Suspense>
                  ) : null}
                </div>
                <Suspense fallback={<></>}><AuthorDetails Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} /></Suspense>
              </>
            ) : null}
          </div>
        </div>
      </div>
    );
  };
  export default Card;
