import React from 'react';
import Card from './Elements/Card';

const CollageRows = ({ CardStyle, ThemeStyle, ThemeInfo, largeDiv, smallDiv, Post, completeDataObject, index, clickToShowPopUp, postSizeInPx, adjustWidth, onClickPopUpSlider }) => {
    const sumPost = index % 2;
    const postPosition = ThemeStyle.direction;

    const largeDivStyle = {
        width: `${largeDiv}%`,
        float: sumPost === 0 ? (postPosition === 'right' ? 'right' : 'left') : postPosition === 'right' ? 'left' : 'right',
    };

    const smallDivStyle = {
        width: `${smallDiv}%`,
        float: sumPost === 1 ? (postPosition === 'right' ? 'left' : 'right') : postPosition === 'right' ? 'left' : 'right',
    };

    if (!Post || Post.length === 0) return null;

    return (
        <div className="ts_cs_post_row">
            <div className="ts_cs_post_large" style={largeDivStyle}>
                <Card Post={completeDataObject[Post[0]]} key={`sqr_crd_0_${completeDataObject[Post[0]].id}`} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} index={index} adjustWidth={100} clickToShowPopUp={clickToShowPopUp} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} />
            </div>
            <div className="ts_cs_post_small" style={smallDivStyle}>
                {Post.map((item, idx) => {
                    if (idx === 0) return null; // Skip the first item since it's rendered in the large div
                    const cardData = completeDataObject[item];
                    return (
                        <Card Post={cardData} key={`sqr_crd_${idx}_${item.id}`} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} index={idx} adjustWidth={adjustWidth} clickToShowPopUp={clickToShowPopUp} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} />
                    );
                })}
            </div>
        </div>
    );
};

export default CollageRows;