import React from "react";
import { isTagShop } from "../../../../actions/api";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
const Media = ({ Post, ThemeStyle, index }) => {
  const mediaIcon = isTagShop && !Post.hotspot ? true : false
  const isVideo = [3, 5].includes(Post.type) && (ThemeStyle.autoPlay == 1 || !isTagShop) ? true : false

  return <div className="tb_ct_media_wrap">
    <MediaIcon Post={Post} IconClass="tb_ct_media_icon" show={mediaIcon} hideVideo={ThemeStyle.autoPlay} />
    {isVideo ?
      <Video VideoClass="tb_ct_video" Post={Post} isCover={true} size={100} controls={false} autoPlay={true} muted={true} index={index} />
      : 
      <Image ImageClass="tb_ct_image" Post={Post} size={100} hotspot={Post.hotspot} blurBG={isTagShop} index={index} />
    }
  </div>
}
export default Media